/* Base Styles */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Layout */
.grid-container {
  display: grid;
  grid-template-areas:
    "header"
    "main"
    "footer";
  grid-template-rows: 5rem 1fr 5rem;
  height: 100vh;
}

/* Header/Footer */
header, footer {
  background-color: #203040;
  color: #fff;
}
header a, footer a {
  color: #fff;
}

/* Common Elements */
a {
  text-decoration: none;
}
a:hover {
  color: #ff8000;
}

button {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #a4a4a4;
  cursor: pointer;
}
button.primary {
  background-color: #f0c040;
}
button.block {
  width: 100%;
}

img {
  width: 100%;
  border-radius: 0.5rem;
}

/* Cards */
.card {
  background: #f8f8f8;
  border: 0.1rem solid #c0c0c0;
  border-radius: 0.5rem;
  margin: 1rem;
}

/* Form Layout */
.form {
  max-width: 60rem;
  margin: auto;
  padding: 1rem;
}

/* Flex Layouts */
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.col-1 { flex: 1; min-width: 25rem; }
.col-2 { flex: 2; min-width: 50rem; }
.col-3 { flex: 3; min-width: 75rem; }

/* Responsive Grid for Mobile */
@media (max-width: 768px) {
  .row {
    flex-direction: column;
    align-items: stretch;
  }

  .col-1, .col-2, .col-3 {
    min-width: 100% !important;
    flex: 1 1 100%;
  }

  .search {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }

  .search input, .search button {
    width: 100%;
    border-radius: 0.5rem;
  }

  .dropdown-content {
    position: static;
    width: 100%;
    padding: 0.5rem;
  }

  aside {
    position: static;
    width: 100%;
    transform: none !important;
    height: auto;
    background-color: #f0f0f0;
  }

  .grid-container {
    grid-template-rows: auto 1fr auto;
  }

  .card {
    margin: 0.5rem 0;
  }
}

/* Pagination */
.pagination a {
  padding: 1rem;
  margin: 0.5rem;
  border: 0.1rem solid #a4a4a4;
  border-radius: 0.5rem;
}
.pagination a.active {
  font-weight: bold;
}

/* Alerts */
.alert {
  padding: 1rem;
  border-radius: 0.5rem;
}
.alert-info {
  background-color: #e0e0ff;
}
.alert-danger {
  background-color: #ffe0e0;
}
.alert-success {
  background-color: #eeffe0;
}

/* Chatbox */
.chatbox {
  position: fixed;
  right: 1rem;
  bottom: 1rem;
  max-width: 100%;
  width: 25rem;
