/* Aside Menu */
aside {
  position: fixed;
  width: 30rem;
  height: 100%;
  background-color: #efefef;
  z-index: 1000;
  transform: translateX(-30rem);
  transition: all 0.5s;
}

aside.open {
  transform: translateX(0);
}

button.open-sidebar {
  font-size: 3rem;
  background: none;
  color: white;
}

button.open-sidebar:hover {
  border-color: white;
}

/* Images */
