  padding: 1rem;
}

.card-body > * {
  margin-bottom: 0.5rem;
}

.price {
  font-size: 2rem;
}

/* Ratings */
.rating span {
  color: #f0c040;
}

.rating span:last-child {
  color: #404040;
}

/* Alerts */
.alert {
  padding: 1rem;
  border-radius: 0.5rem;
}

.alert-info {
  background-color: #e0e0ff;
}

.alert-danger {
  background-color: #ffe0e0;
}

.alert-success {
  background-color: #eeffe0;
}

/* Forms */
.form {
  max-width: 60rem;
  margin: 0 auto;
}

.form > div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

/* Dropdown Menus */
.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  right: 0;
  min-width: 12rem;
  padding: 1rem;
  background-color: #203040;
  border-radius: 0.5rem;
}

.dropdown:hover .dropdown-content {
  display: block;
}

/* Checkout Steps */
.checkout-steps > div {
  border-top: 0.3rem solid #c0c0c0;
  flex: 1;
  padding: 1rem;
  font-weight: bold;
}

.checkout-steps > div.active {
  border-top-color: #f08000;
  color: #f08000;
}

/* Tables */
.table {
  width: 100%;
  border-collapse: collapse;
}

.table tbody tr:nth-of-type(odd) {
  background-color: #f4f4f4;
}

.table td,
.table th {
  border: 0.1rem solid #e4e4e4;
  padding: 0.5rem;
}

/* Carousel */
.carousel .slide img {
  max-width: 30rem;
}

/* Search Bar */
.search button {
  border-radius: 0 0.5rem 0.5rem 0;
}

.search input {
  border-radius: 0.5rem 0 0 0.5rem;
}

/* Dashboard Summary */
.summary > li {
  border: 0.1rem solid #c0c0c0;
  margin: 2rem;
  border-radius: 0.5rem;
  flex: 1 1 20rem;
}

.summary-title {
  font-size: 2rem;
}

.summary-body {
  font-size: 4rem;
  text-align: center;
}

/* Chatbox */
